@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400&display=swap');


*,
*::after,
*::before {
	box-sizing: border-box;
}

:root {
	font-size: 18px;
}


body {
	margin: 0;
	color: #000;
	background-color: #191919;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font-family: 'Inter';
	font-weight: 400;
	color: #fff;
	letter-spacing: -1px;
}
a {
	color: #fff;
}


/* Page Loader */
.js .loading::before,
.js .loading::after {
	content: '';
	position: fixed;
	z-index: 1000;
}

.js .loading::before {
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: var(--color-bg);
}

.js .loading::after {
	top: 50%;
	left: 50%;
	width: 60px;
	height: 60px;
	margin: -30px 0 0 -30px;
	border-radius: 50%;
	opacity: 0.4;
	background: var(--color-link);
	animation: loaderAnim 0.7s linear infinite alternate forwards;

}

@keyframes loaderAnim {
	to {
		opacity: 1;
		transform: scale3d(0.5,0.5,1);
	}
}


.unbutton {
	background: none;
	border: 0;
	padding: 0;
	margin: 0;
	font: inherit;
}

.unbutton:focus {
	outline: none;
}

/* SVG */
svg#main-logo {
	transition: fill 0.7s;
	max-width: 90vw;
	margin: 2px auto 0px auto;
}
svg {
	overflow: visible;
}

.content {
	display: block;
	width: 300px;
	max-width: 90%;
	margin: 10px auto 0 auto;
	text-align: center;
	display: flex;
	justify-content: center;
}

footer {
	position: fixed;
	bottom: 10px;
	width: 100%;
	margin: 0 auto;
	text-align: center;
	display: flex;
	justify-content: center;
	flex-direction: column;
}
footer p {
	margin: 0;
	line-height: 80%;
}
footer p > a {
	text-decoration: underline;
}



#canvasContainer{
	position: fixed;
	top: 0;
	left: 0;
	z-index: -1;
	width: 100%;
	height: 100%;
}
#canvasContainer img{
	visibility: hidden;
	pointer-events: none;
	position: absolute;
}
